export function getSupabaseAuthKeys() {
  return Object.keys(localStorage).filter(k =>
    k.startsWith("sb-") && k.includes("-auth-token")
  );
}

// Save session keys after successful login
export function saveValidSupabaseKeys() {
  const keys = getSupabaseAuthKeys();
  if (keys.length === 0) return;

  const store: Record<string, string> = {};
  keys.forEach(k => (store[k] = localStorage.getItem(k) || ""));

  localStorage.setItem("__thv_valid_supabase_keys__", JSON.stringify(store));
}

// Restore missing / damaged keys BEFORE login
export function repairSupabaseKeysIfBroken() {
  const saved = localStorage.getItem("__thv_valid_supabase_keys__");
  if (!saved) return;

  const good = JSON.parse(saved);
  Object.keys(good).forEach(k => {
    if (!localStorage.getItem(k)) {
      console.log("üõ† Restoring missing Supabase key:", k);
      localStorage.setItem(k, good[k]);
    }
  });
}

// Remove corrupted keys
export function clearStaleSupabaseKeys() {
  const saved = JSON.parse(localStorage.getItem("__thv_valid_supabase_keys__") || "{}");
  const current = getSupabaseAuthKeys();

  current.forEach(k => {
    if (!saved[k]) {
      console.log("üß® Removing stale Supabase key:", k);
      localStorage.removeItem(k);
    }
  });
}
