import { clearStaleSupabaseKeys } from "./session-repair";
import { supabase } from "./supabaseClient";






export const handleLogout = async ({ onSuccess, onFail } = {}) => {
  console.log("üîê Logging out‚Ä¶");

  // Remove corrupted keys ONLY
  clearStaleSupabaseKeys();

  // Call Supabase logout
  const { error } = await supabase.auth.signOut();

  if (error) {
    console.error("‚ùå Logout error:", error);
    return onFail?.(error.message);
  }

  // Clear *all* session keys (safe now)
  clearStaleSupabaseKeys();

  // Optionally wipe saved session map
  localStorage.removeItem("__thv_valid_supabase_keys__");

  onSuccess?.();
};





export const handleSignup = async ({
  email,
  password,
  fullName,
  onSuccess,
  onFail,
}: {
  email: string
  password: string
  fullName?: string
  onSuccess?: () => void
  onFail?: (error?: string) => void
}) => {
  const { data, error } = await supabase.auth.signUp({
    email,
    password,
    options: {
      data: {
        full_name: fullName || "unknown",
        signup_source: "thv-web", // optional
      },
    },
  })

  if (error) onFail?.(error.message)
  else onSuccess?.()
}


export const handleLogin = async (
  { email, password, onSuccess, onFail }: {
    email: string
    password: string
    onSuccess?: () => void
    onFail?: (error?: string) => void
  }
) => {
  const { data, error } = await supabase.auth.signInWithPassword({ email, password })
  if (error) onFail?.(error.message)
  else onSuccess?.()
}

export const handleResetPassword = async (
  { email, onSuccess, onFail }: {
    email: string
    onSuccess?: () => void
    onFail?: (error?: string) => void
  }
) => {
  const { data, error } = await supabase.auth.resetPasswordForEmail(email)
  if (error) onFail?.(error.message)
  else onSuccess?.()
}

export const handleUpdatePassword = async (
  { newPassword, onSuccess, onFail }: {
    newPassword: string
    onSuccess?: () => void
    onFail?: (error?: string) => void
  }
) => {
  const { data, error } = await supabase.auth.updateUser({ password: newPassword })
  if (error) onFail?.(error.message)
  else onSuccess?.()
}



export interface UpdateUserData {
  fullName?: string;
  phone?: string;
  email?: string;
  dob?: string;
  address?: string;
  hometown?: string;
  field?: string;
  goals?: string;
  vision?: string;
  status?: string;
  avatar?: string;
}

/**
 * Updates user profile data across both tables: user_member & user_private
 * Automatically maps field names to Supabase column names.
 */
export const updateUserProfile = async (updates: UpdateUserData) => {
  try {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) throw new Error('Not logged in');

    const memberPayload: any = {};
    const privatePayload: any = {};

    if (updates.fullName) memberPayload.full_name = updates.fullName;
    if (updates.hometown) memberPayload.hometown = updates.hometown;
    if (updates.field) memberPayload.field_of_expertise = updates.field;
    if (updates.status) memberPayload.profile_status = updates.status;
    if (updates.avatar) memberPayload.avatar_url = updates.avatar;

    if (updates.phone) privatePayload.phone = updates.phone;
    if (updates.email) privatePayload.email = updates.email;
    if (updates.address) privatePayload.address = updates.address;
    if (updates.dob) privatePayload.dob = updates.dob;
    if (updates.goals) privatePayload.personal_goals = updates.goals;
    if (updates.vision) privatePayload.future_vision = updates.vision;

    // Run updates separately depending on which data is provided
    if (Object.keys(memberPayload).length > 0)
      await supabase.from('user_member').update(memberPayload).eq('id', user.id);

    if (Object.keys(privatePayload).length > 0)
      await supabase.from('user_private').update(privatePayload).eq('id', user.id);

    return { success: true };
  } catch (error) {
    console.error('Update user profile failed:', error);
    return { success: false, error };
  }
};
