import React, { useState } from "react"
import { handleUpdatePassword } from "../../database/functions"


export default function UpdatePassword() {
  const [newPassword, setNewPassword] = useState("")
  const [confirmPassword, setConfirmPassword] = useState("")
  const [message, setMessage] = useState("")

  const onSuccess = () => setMessage("‚úÖ Password updated successfully!")
  const onFail = (err?: string) => setMessage("‚ùå " + (err || "Password update failed."))

  const handleSubmit = (e: any) => {
    e.preventDefault()
    if (newPassword !== confirmPassword) {
      setMessage("‚ùå Passwords do not match.")
      return
    }
    handleUpdatePassword({ newPassword, onSuccess, onFail })
  }

  return (
    <div style={{ fontFamily: "sans-serif", textAlign: "center", marginTop: "15vh", color: "#0a0a0a" }}>
      <h1>Tinh Hoa Vi·ªát Password Reset</h1>
      <p style={{ marginBottom: "2rem" }}>Enter your new password below to complete the reset process.</p>

      <form
        onSubmit={handleSubmit}
        style={{ display: "flex", flexDirection: "column", gap: "10px", maxWidth: "320px", margin: "0 auto" }}
      >
        <input
          type="password"
          placeholder="New password"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
          style={{ padding: "10px", border: "1px solid #ccc", borderRadius: "5px" }}
        />
        <input
          type="password"
          placeholder="Confirm password"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          style={{ padding: "10px", border: "1px solid #ccc", borderRadius: "5px" }}
        />
        <button
          type="submit"
          style={{
            padding: "10px",
            background: "#2E7D32",
            color: "#fff",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
          }}
        >
          Update Password
        </button>
      </form>

      {message && <p style={{ marginTop: "1.5rem", fontSize: "1rem", color: "#333" }}>{message}</p>}
    </div>
  )
}
